.gallery__list {
  display: grid;
  grid-template-columns: 230px;
  grid-template-rows: 230px;
  -webkit-column-gap: 27px;
     -moz-column-gap: 27px;
          column-gap: 27px;
  row-gap: 34px;
  margin: 0;
  padding: 0;
  list-style: none; }

.gallery picture {
  line-height: 0; }

.gallery__item:nth-child(1n) {
  grid-column: 1 / 2;
  grid-row: 1 / 2; }

.gallery__item:nth-child(2n) {
  grid-column: 1 / 2;
  grid-row: 2 / 3; }

.gallery__item:nth-child(3n) {
  grid-column: 2 / 3;
  grid-row: 1 / 3; }

.gallery__item:nth-child(4n) {
  grid-column: 3 / 4;
  grid-row: 1 / 2; }

.gallery__item:nth-child(5n) {
  grid-column: 3 / 4;
  grid-row: 2 / 3; }

.gallery__item:nth-child(6n) {
  grid-column: 4 / 5;
  grid-row: 1 / 2; }

.gallery__item:nth-child(7n) {
  grid-column: 4 / 5;
  grid-row: 2 / 3; }

.gallery__image {
